.content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;

    &__title {
        letter-spacing: 2px;
        margin-bottom: 13px;

        @include atSmall {
            font-size: 2.75rem;
            margin-bottom: 26px;

        }

        @include atMedium {
            font-size: 4rem;
        }
    }

    &__info {
        font-size: 1.05rem;
        font-weight: 300;

        @include atSmall {
            font-size: 1.15rem;
            line-height: 30px;
            word-spacing: 2px;
        }

        @include atMedium {
            font-size: 1.25rem;
        }
    }

    &__btn {
        border: 1px solid #FFF;
        display: inline-block;
        margin-top: 68.7px;
        text-transform: uppercase;
        padding: 15px 30px;

        &:hover {
            border: 1px solid #DDD;
            color: #DDD;
        }

        @include atSmall {
            font-size: 1.15rem;
        }

        @include atMedium {
            font-size: 1.25rem;
            padding: 21px 59px;
        }

        &--small-margin {
          margin-top: 20px;
        }
    }

}
