html, body {
  width: 100%;
  height: 100%; }

a {
  color: #FFF;
  font-weight: 300;
  text-decoration: none; }
  a:visited {
    color: #FFF; }

body {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../images/background--small.jpg") center center no-repeat;
  background-size: cover;
  color: #FFF;
  font-family: 'Lato', sans-serif;
  position: relative;
  text-align: center; }
  @media (min-width: 800px) {
    body {
      background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../images/background.jpg") center center no-repeat;
      background-size: cover; } }
  @media (min-width: 1100px) {
    body {
      background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../images/background--large.jpg") center center no-repeat;
      background-size: cover; } }

h1 {
  margin: 0; }

p {
  margin: 0; }

.wrapper {
  margin: 0 auto;
  max-width: 548px;
  padding-left: 20px;
  padding-right: 20px; }
  @media (min-width: 800px) {
    .wrapper {
      padding-left: 0;
      padding-right: 0; } }

.content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%; }
  .content__title {
    letter-spacing: 2px;
    margin-bottom: 13px; }
    @media (min-width: 530px) {
      .content__title {
        font-size: 2.75rem;
        margin-bottom: 26px; } }
    @media (min-width: 800px) {
      .content__title {
        font-size: 4rem; } }
  .content__info {
    font-size: 1.05rem;
    font-weight: 300; }
    @media (min-width: 530px) {
      .content__info {
        font-size: 1.15rem;
        line-height: 30px;
        word-spacing: 2px; } }
    @media (min-width: 800px) {
      .content__info {
        font-size: 1.25rem; } }
  .content__btn {
    border: 1px solid #FFF;
    display: inline-block;
    margin-top: 68.7px;
    text-transform: uppercase;
    padding: 15px 30px; }
    .content__btn:hover {
      border: 1px solid #DDD;
      color: #DDD; }
    @media (min-width: 530px) {
      .content__btn {
        font-size: 1.15rem; } }
    @media (min-width: 800px) {
      .content__btn {
        font-size: 1.25rem;
        padding: 21px 59px; } }
    .content__btn--small-margin {
      margin-top: 20px; }

footer {
  bottom: 0;
  font-weight: 300;
  margin-bottom: 5px;
  position: absolute;
  text-align: center;
  width: 100%; }
  footer p:first-of-type {
    margin-bottom: 19px; }
